"use client";

import Link from "next/link";
import { useMemo, useState } from "react";

import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";

type CaseRow = {
  id: string;
  user_display: string;
  tool_name: string;
  risk_label: "ROUTINE" | "ESCALATE" | "BLOCK";
  risk_score: number;
  status: "PENDING_REVIEW" | "APPROVED" | "REJECTED" | "NEEDS_MORE_INFO";
  created_at: string;
};

function riskBadge(label: CaseRow["risk_label"]) {
  if (label === "BLOCK") return <Badge variant="destructive">BLOCK</Badge>;
  if (label === "ESCALATE") return <Badge className="bg-orange-500 text-white hover:bg-orange-500">ESCALATE</Badge>;
  return <Badge variant="secondary">ROUTINE</Badge>;
}

function statusBadge(status: CaseRow["status"]) {
  if (status === "APPROVED") return <Badge className="bg-green-600 text-white hover:bg-green-600">APPROVED</Badge>;
  if (status === "REJECTED") return <Badge variant="destructive">REJECTED</Badge>;
  if (status === "NEEDS_MORE_INFO") return <Badge className="bg-yellow-500 text-white hover:bg-yellow-500">NEEDS_MORE_INFO</Badge>;
  return <Badge variant="secondary">PENDING_REVIEW</Badge>;
}

function rowTone(status: CaseRow["status"]) {
  if (status === "APPROVED") return "bg-green-50/40";
  if (status === "REJECTED") return "bg-red-50/40";
  if (status === "NEEDS_MORE_INFO") return "bg-yellow-50/40";
  return "";
}

type Props = {
  cases: CaseRow[];
};

export default function CasesTableClient({ cases }: Props) {
  const [q, setQ] = useState("");
  const [tab, setTab] = useState<"PENDING" | "APPROVED" | "REJECTED" | "ALL">("PENDING");
  const [riskFilter, setRiskFilter] = useState<"ALL" | "ROUTINE" | "ESCALATE" | "BLOCK">("ALL");
  const [sortKey, setSortKey] = useState<"NEWEST" | "RISK_DESC" | "RISK_ASC">("RISK_DESC");

  const filtered = useMemo(() => {
    const needle = q.trim().toLowerCase();

    let list = cases.slice();

    // Tabs
    if (tab === "PENDING") {
      list = list.filter((c) => c.status === "PENDING_REVIEW" || c.status === "NEEDS_MORE_INFO");
    } else if (tab === "APPROVED") {
      list = list.filter((c) => c.status === "APPROVED");
    } else if (tab === "REJECTED") {
      list = list.filter((c) => c.status === "REJECTED");
    }

    // Risk filter
    if (riskFilter !== "ALL") list = list.filter((c) => c.risk_label === riskFilter);

    // Search
    if (needle) {
      list = list.filter((c) => {
        return (
          c.id.toLowerCase().includes(needle) ||
          c.user_display.toLowerCase().includes(needle) ||
          c.tool_name.toLowerCase().includes(needle)
        );
      });
    }

    // Sort
    list.sort((a, b) => {
      if (sortKey === "NEWEST") return b.created_at.localeCompare(a.created_at);
      if (sortKey === "RISK_ASC") return a.risk_score - b.risk_score;
      return b.risk_score - a.risk_score;
    });

    return list;
  }, [cases, q, tab, riskFilter, sortKey]);

  const counts = useMemo(() => {
    const pending = cases.filter((c) => c.status === "PENDING_REVIEW" || c.status === "NEEDS_MORE_INFO").length;
    const approved = cases.filter((c) => c.status === "APPROVED").length;
    const rejected = cases.filter((c) => c.status === "REJECTED").length;
    return { pending, approved, rejected, all: cases.length };
  }, [cases]);

  return (
    <div className="space-y-4">
      <div className="flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
        <Tabs value={tab} onValueChange={(v) => setTab(v as any)}>
          <TabsList>
            <TabsTrigger value="PENDING">Pending ({counts.pending})</TabsTrigger>
            <TabsTrigger value="APPROVED">Approved ({counts.approved})</TabsTrigger>
            <TabsTrigger value="REJECTED">Rejected ({counts.rejected})</TabsTrigger>
            <TabsTrigger value="ALL">All ({counts.all})</TabsTrigger>
          </TabsList>
        </Tabs>

        <div className="flex flex-col gap-2 md:flex-row md:items-center">
          <Input
            value={q}
            onChange={(e) => setQ(e.target.value)}
            placeholder="Search case id, user, tool..."
            className="md:w-[320px]"
          />

          <Select value={riskFilter} onValueChange={(v) => setRiskFilter(v as any)}>
            <SelectTrigger className="md:w-[170px]">
              <SelectValue placeholder="Risk filter" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="ALL">All risk</SelectItem>
              <SelectItem value="ROUTINE">Routine</SelectItem>
              <SelectItem value="ESCALATE">Escalate</SelectItem>
              <SelectItem value="BLOCK">Block</SelectItem>
            </SelectContent>
          </Select>

          <Select value={sortKey} onValueChange={(v) => setSortKey(v as any)}>
            <SelectTrigger className="md:w-[190px]">
              <SelectValue placeholder="Sort" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="RISK_DESC">Risk: high to low</SelectItem>
              <SelectItem value="RISK_ASC">Risk: low to high</SelectItem>
              <SelectItem value="NEWEST">Newest first</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="rounded-lg border bg-background overflow-hidden">
        <div className="grid grid-cols-12 gap-2 border-b bg-muted/30 px-4 py-3 text-xs font-semibold">
          <div className="col-span-2">Case ID</div>
          <div className="col-span-2">User</div>
          <div className="col-span-4">Tool</div>
          <div className="col-span-2">Verdict</div>
          <div className="col-span-1 text-right">Score</div>
          <div className="col-span-1">Status</div>
        </div>

        {filtered.length === 0 ? (
          <div className="px-4 py-10 text-sm text-muted-foreground">
            No cases match your filters.
          </div>
        ) : (
          <div>
            {filtered.map((c) => (
              <div
                key={c.id}
                className={
                  "grid grid-cols-12 gap-2 px-4 py-3 text-sm border-b last:border-b-0 " +
                  rowTone(c.status)
                }
              >
                <div className="col-span-2">
                  <Link className="underline" href={`/cases/${c.id}`}>
                    {c.id}
                  </Link>
                </div>
                <div className="col-span-2">{c.user_display}</div>
                <div className="col-span-4 font-mono text-xs leading-5">{c.tool_name}</div>
                <div className="col-span-2">{riskBadge(c.risk_label)}</div>
                <div className="col-span-1 text-right tabular-nums">{c.risk_score}</div>
                <div className="col-span-1">{statusBadge(c.status)}</div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="text-xs text-muted-foreground">
        Risk label is the model recommendation. Status is the human decision outcome.
      </div>
    </div>
  );
}
